<template>
  <div class="my-insured">
    <div class="add-btn-wrapper"><p class="add-btn" @click="goAdd">添加常用被保险人</p></div>
    <ul class="insured-con">
      <li v-for="(item,index) in insuredList" :key="index" >
        <div class="insured-info clearfix">
          <span class="name">{{item.name}}</span>
          <span class="relation">{{getRelation(item.type)}}</span>
          <span class="idNum">身份证号：{{item.id_num}}</span>
          <span class="more" :style="moreArr[index] ? downImg : upImg" @click="detailShow(index)">更多</span>
          <span class="edit" :style="item.is_checked?noEditImg:editImg"></span>
          <span class="delete" @click="del(item)"></span>
        </div>
        <div class="more-info"  v-if="moreArr[index]">
          <span class="mobile" v-if="item.mobile">手机号：{{item.mobile }}</span>
          <span class="mobile " v-else>手机号：<i>尚未填写</i></span>
          <span class="addr" v-if="item.addr||item.addr_detail">地址：{{item.addr}}{{item.addr_detail}}</span>
          <span class=" addr " v-else>地址：<i>尚未填写</i></span>
          <span class="email" v-if="item.email">地址邮箱：{{item.email }}</span>
          <span class="email " v-else>地址邮箱：<i>尚未填写</i></span>
        </div>
      </li>
    </ul>
    <alert-box v-if="alertShow"
      :width='420'
      :height='144'
      :btnText="'删除'"
               @hanleClose="closeAlert"
      @handleBtnEvent="handleDelete"
      :absTop='232'
      :absLeft="148"
      :title="'是否确认删除此常用被保险人？'"></alert-box>
    <insured ></insured>
  </div>
</template>

<script>
import alertBox from '@/components/BaseAlertBox'
import insured from '@/components/TheInsured.vue'
import { getRelation } from '@/assets/js/mixins/base.js'
export default {
  name: 'myInsured',
  mixins: [getRelation],
  data () {
    return {
      alertShow: false,
      delId: '',
      delType: '',
      page: 1,
      limit: 10,
      currentMore: null,
      moreShow: false,
      insuredList: {},
      editImg: {
        'background-image':
        'url(' + require('@/assets/images/icon_edit.png') + ')'
      },
      noEditImg: {
        'background-image':
        'url(' + require('@/assets/images/icon_no_edit.png') + ')'
      },
      upImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_alow.png') + ')'
      },
      downImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      moreArr: []
    }
  },
  mounted () {
    this.getInsurdList()
  },
  methods: {
    goAdd(){

    },
    getInsurdList () {
      let postData = {
        page: this.page,
        limit: this.limit
      }
      this.$api.personCenter.getInsured(postData).then(res => {
        if (res.code === 1) {
          this.moreArr.push(false)
          this.insuredList = res.data.data
          console.log(998877, this.insuredList)

          this.insuredList=this.insuredList.filter((item) => {
            return item.type !== 6
          })
          // console.log(1111111, this.insuredList)
          //
          // if (res.data.data[index].type === 6) {
          //   this.insuredList.splice(index, 1)
          // }
        }
      })
    },
    detailShow (index) {
      this.moreArr.forEach((item, i) => { // 循环已经把所有的状态值清空了
        if (index !== i) {
          this.moreArr.splice(i, 1, false)
        }
      })
      this.moreArr.splice(index, 1, !this.moreArr[index])
    },
    del (item) {
      this.alertShow = true
      this.delId = item.id
      this.delType = item.type
    },
    handleDelete () {
      this.alertShow = false
      let postData = {
        id: this.delId,
        type: this.delType
      }
      this.$api.personCenter.deleteInsured(postData).then(res => {
        if (res.code === 1) {
          this.getInsurdList()
        }
      })
    },
    closeAlert () {
      this.alertShow = false
    }
  },
  components: {
    alertBox,
    insured
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .fade-enter-active, .fade-leave-active {
    transition: all .5s ease
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
  {
    opacity: 0;
  }

  .my-insured {
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(219, 219, 219, 0.35);
    .add-btn-wrapper {
      border-bottom: 1px solid #F1F4F4;
      padding: 20px 0 20px 20px;
      .add-btn {
        cursor: pointer;
        width: 168px;
        height: 44px;
        line-height: 44px;
        font-size: 16px;
        color: #FFFFFF;
        text-align: center;
        background: $color-active-text;
        border-radius: 22px;

      }
    }
    .insured-con {
      li {
        .insured-info {
          height: 60px;
          line-height: 60px;
          border-bottom: 1px solid #F1F4F4;
          font-size: 0;
          &:hover {
            background: #F1F4F4;
          }
          span {
            float: left;
            font-size: 16px;
            color: $color-text;
            text-align: left;
          }
          .name {
            width: 92px;
            padding-left: 20px;
          }
          .relation {
            width: 92px;
          }
          .idNum {
            width: 485px;
          }
          .more {
            cursor: pointer;
            color: #A5A8A8;
            width: 54px;
            background-size: 12px 8px;
            background-repeat: no-repeat;
            background-position: right center;
          }
          .edit {
            cursor: pointer;
            /*background: url("../../../assets/images/icon_edit.png") no-repeat left center;*/
            background-size: 28px 28px;
            width: 28px;
            height: 28px;
            margin: 0 28px 0 60px;
            padding-top: 32px;
            background-repeat: no-repeat;
            background-position: left center;
          }
          .delete {
            cursor: pointer;
            background: url("../../../assets/images/icon_delete.png") no-repeat left center;
            background-size: 28px 28px;
            width: 28px;
            height: 28px;
            padding-top: 32px;
          }
        }
        .more-info {
          height: 47px;
          line-height: 47px;
          font-size: 0;
          border-bottom: 1px solid #F1F4F4;
          span {
            display: inline-block;
            font-size: 14px;
            color: $color-text;
            i{
              color: #D5D8D8;
            }
          }
          .mobile {
            width: 184px;
            padding-left: 40px;
          }
          .addr {
            width: 367px;
          }

        }
      }
    }
  }
</style>
