<template>
       <div class="alert-box"  :style="alertStyle">
         <p class="title">{{title}}</p>
         <div class="alert-btn-wrapper clearfix">
           <p class="alert-btn" @click="handleBtnEvent">{{btnText}}</p>
         </div>
           <i class="close" @click="close"></i>
       </div>
</template>

<script>
export default {
  name: 'BaseAlertBox',
  props: {
    title: {
      type: String,
      default: ''
    },
    btnText: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    width: {
      type: Number
    },
    height: {
      type: Number
    },
    absTop: {
      type: Number,
      default: 0
    },
    absLeft: {
      type: Number,
      default: 0
    }
  },
  methods: {
    close () {
      this.$emit('hanleClose')
    },
    handleBtnEvent () {
      this.$emit('handleBtnEvent')
    }
  },
  computed: {
    alertStyle () {
      return {
        width: this.width ? this.width + 'px' : '100%',
        height: this.height ? this.height + 'px' : '100%',
        top: this.absTop + 'px',
        left: this.absLeft + 'px'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
  .alert-box {
    position: absolute;
    z-index: 101;
    background: #fff;
    /*box-shadow: 0 8px 16px 0 rgba(219,219,219,0.84);*/
    box-shadow: 0 12px 28px 0 #B6BBBB;
    border-radius: 4px;
    .title{
      font-size: 16px;
      color: #2A3449;
      padding:40px 0 32px 20px;
    }
    .alert-btn-wrapper{
      text-align: right;
      padding-right:20px;
      .alert-btn{
        cursor: pointer;
        float: right;
        font-size: 14px;
        color: #FF4B4B;
        border: 1px solid #FF4B4B;
        border-radius: 18px;
        width: 70px;
        height:36px;
        line-height: 36px;
        text-align: center;
      }
    }

    .close {
      cursor: pointer;
      position: absolute;
      top: 16px;
      right: 17px;
      width: 22px;
      height: 22px;
      background: url("../assets/images/close.png");
      background-size: 22px 22px;
    }
  }
</style>
